export default {
	data() {
		return {
			phoneHeight:'',
			id:0, // 使用 marker点击事件 需要填写id
			title: 'map',
			latitude: 39.909,
			longitude: 116.39742,
			covers: [{
				latitude: 39.909,
				longitude: 116.39742,
				iconPath: '../../../static/location.png'
			}, {
				latitude: 39.90,
				longitude: 116.39,
				iconPath: '../../../static/location.png'
			}]
		}
	},
	onReady(){
		// 计算屏幕高度 ，宽度
		let _this = this;
		uni.getSystemInfo({
			success: (res) => {
				 _this.phoneHeight = res.windowHeight*0.58;
			}
		});
	},
	methods: {
		back(){
			uni.navigateTo({
			    url: '../myappointment/myappointment'
			});
		}
	}
}
